.App {
  font-family: 'Segoe UI', Arial, sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
body{
  background-color: #00695c;
}
.calculator-container {
  min-width: 350px;
  max-width: 400px;
  margin: auto;
  padding: 2rem 2.5rem;
  background: #ffffffcc;
  border-radius: 18px;
  box-shadow: 0 8px 32px rgba(0,0,0,0.18);
  display: flex;
  flex-direction: column;
  align-items: center;
}

label {
  margin-top: 1rem;
  font-weight: 500;
  color: #333;
}

input {
  margin-top: 0.3rem;
  margin-bottom: 1rem;
  padding: 0.5rem 1rem;
  border: 1px solid #b2dfdb;
  border-radius: 8px;
  font-size: 1rem;
  width: 100%;
  box-sizing: border-box;
  background: #e0f7fa;
  transition: border 0.2s;
}

input:focus {
  border-color: #00897b;
  outline: none;
}

button {
  margin: 0.5rem 0.3rem;
  padding: 0.6rem 1.2rem;
  background: #00897b;
  color: #fff;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.2s;
}

button:hover {
  background: #00695c;
}

hr {
  width: 100%;
  margin: 2rem 0 1rem 0;
  border: none;
  border-top: 2px solid #b2dfdb;
}

h2 {
  color: #00897b;
  font-size: 2.2rem;
  margin-top: 0.5rem;
  font-weight: 700;
  letter-spacing: 1px;
}